* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

nav {
  color: #fff;
  text-align: center;
  background-color: #333;
  padding: 10px 20px;
}

nav .logo {
  font-size: 24px;
  font-weight: bold;
}

nav .nav-links a {
  color: #fff;
  margin: 0 15px;
  font-size: 16px;
  text-decoration: none;
}

header {
  text-align: center;
  margin-bottom: 40px;
}

header img {
  width: 100%;
  height: auto;
}

footer {
  color: #fff;
  text-align: center;
  background-color: #333;
  margin-top: 40px;
  padding: 10px 0;
}

#contact {
  background-color: #949b9f48;
}

h1, h2 {
  color: #007acc;
  margin-bottom: 20px;
}

p {
  margin-bottom: 20px;
  line-height: 1.6;
}

input, input[type="text"], input[type="email"], textarea {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  margin: 10px 0;
  padding: 10px;
}

button, button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: #007acc;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
}

section {
  width: 800px;
  margin: auto auto 40px;
  padding: 20px;
}

.intro-text {
  float: right;
  width: 50%;
  padding: 20px;
}

.intro-image {
  float: left;
  width: 50%;
}

.intro-image img {
  width: 100%;
  height: auto;
}

.form-group {
  margin-bottom: 20px;
}

.clearfix:after {
  content: "";
  clear: both;
  display: table;
}
